.food-item{
    width: 100%;
    margin: auto;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #00000015;
    transition: 0.3s;
    animation: fadeIn 0.5ms;
}
.food-item-image{
    width: 100%;
    border-radius: 15px 15px 0px 0px;
}
.food-item-info{
    padding: 20px;
}
.food-item-name-rating{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    font-style: oblique;
    color: purple;
    font-weight: 500px;
}
.food-item-name-rating p{
    font-size: 20px;
    font-weight: 500;
}
.food-item-name-rating img{
    width: 70px;
}
.food-item-desc{
    color: rgb(113, 108, 108);
    font-size: 15px;
    font-style: oblique;
}
.food-item-info .food-item-price{
    color: orangered;
    font-size: 22px;
    font-weight: 500;
    margin-left: 10px 0px;
    text-align: start;
}
.food-item-img-container{
    position: relative;
}
.food-item-img-container .add{
    width:35px;
    position: absolute;
    bottom: 15px;
    right: 15px;
    cursor: pointer;
    border-radius: 50%;

}
.food-item-counter {
    position: absolute;
    bottom: 15px;
    right:15px;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px;
    border-radius: 50px;
    background-color: aliceblue;
}
.food-item-counter img{
    width: 30px;
}