@import url('https://fonts.googleapis.com/css2?family=Bungee+Spice&family=Foldit:wght@100..900&display=swap');
.footer{
    color: white;
    background-color: rgb(53, 49, 45);
    display: flex;
    flex-direction:column ;
    align-items: center;
    gap:20px;
    padding: 20px 8vw;
    padding-top: 80px;
    margin-top: 100px;
}
.footer h1{
    font-family: "Bungee Spice", sans-serif;
  font-weight: 500;
  font-style: oblique;
  
      
}
.footer h1 span{
    margin-left:40px;
}
.footer-content{
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    gap:80px;
}
.footer-content-left,.footer-content-right,.footer-content-center{
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 20px;
}
.footer-content-left li,.footer-content-right li,.footer-content-center li{
    list-style: none;
    margin-bottom: 10px;
    cursor: pointer;
}
.footer-content-right h2,.footer-content-center h2{
    color: aliceblue;
}
.footer-social-icons img{
    width:40px;
    margin-right: 15px;
}
.footer hr{
    width: 100%;
    height: 2px;
    margin: 20px 0;
    background-color: gray;
    border: none;
}
@media (max-width:750px){
     .footer-content{
        display: flex;
        flex-direction: column;
        gap: 30px;
     }
     .footer-content-right{
        text-align: center;
     }
}
